<template>
    <v-container>
        <v-row style="margin-top: 80px;">
            <v-col class="text-center my-5">
                <h1 class="display-1" style="font-weight:bold"></h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col class="text-center">
                <form @submit.prevent="fnRegisterUser">
                    <v-text-field name="Email" label="" type="email" v-model="sEmail" required style="width: 400px;margin: 0 auto;"></v-text-field>
                    <v-text-field name="Password" label="" type="password" v-model="sPassword" required style="width: 400px;margin: 0 auto;"></v-text-field>
                    <!--     rules   -->
                    <v-text-field name="ConfirmPassword" label=" " type="password" v-model="sConfirmPassword" required :rules="[fnComparePassword]" style="width: 400px;margin: 0 auto;"></v-text-field>
                    <v-col>
                    <v-btn type="submit" color="#00B0FF" dark style="width: 400px;margin: 0 auto;"><v-icon left>mdi-account</v-icon></v-btn>
                </v-col>
                </form>
            </v-col>
        </v-row>
        <v-row style="margin-top: 100px;">
            <v-col class="text-center mt-10">
                <h1 class="display-1" style="color: #868686;">  ?</h1>
            </v-col>
        </v-row>
        <v-col class="mt-5 text-center">
                <v-btn to="/login" large style="width: 400px;" color="#00B0FF" dark outlined>
                    <v-icon left>mdi-login</v-icon>
                      
                </v-btn>
            </v-col>
    </v-container>
</template>

<script>
    export default {
        data() {
            return {
                sEmail:'',
                sPassword:'',
                sConfirmPassword:'',
            }
        },
        computed:{
            fnComparePassword(){
                if (this.sPassword == this.sConfirmPassword) return true
                else return '  .'
            },
            fnCompareEmail(){
                if (e.code == 'email-already-in-use') return true
                else return '   .'
            }
        },
        methods:{
            fnRegisterUser(){
                if (this.fnComparePassword) {
                    this.$store.dispatch('fnRegisterUser', {
                        pEmail:this.sEmail,
                        pPassword:this.sPassword
                    })
                }
            }
        }
    }
</script>

