<template>
    <v-container>
        <v-row>
            <v-col cols="12" class="text-center my-5">
                <h1 class="display-1"> </h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="8" offset="2" class="text-center">
                <form @submit.prevent="fnDoLogin">
                    <v-text-field v-model="sEmail" name="Email" label="" type="email" required></v-text-field>
                    <v-text-field v-model="sPassword" name="Password" label="" type="password" required ref="passnum"></v-text-field>
                    <v-btn v-if="!fnGetLoading" type="submit" color="orange" dark></v-btn>
                    <!--        -->
                    <v-progress-circular v-if="fnGetLoading" indeterminate :width="7" :size="70" color="grey lighten-1"></v-progress-circular>
                    <!--      -->
                    <v-alert class="mt-3" type="error" dismissible v-model="bAlert">
                        {{ fnGetErrMsg  }}
                    </v-alert>
                </form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        data(){
            return{
                sEmail:'',
                sPassword:'',
                bAlert:false
            }
        },
        methods:{
            fnDoLogin(){
                this.$store.dispatch('fnDoLogin', {pEmail:this.sEmail, pPassword:this.sPassword})
            }
        },
        computed:{
            fnGetLoading(){
                return this.$store.getters.fnGetLoading;
            },
            fnGetErrMsg(){
                return this.$store.getters.fnGetErrorMessage
            }
        },
        watch:{
            bAlert(pValue){
                console.log(pValue)
                if (pValue == false){
                    if (this.fnGetErrMsg !== '  .') this.$router.push('/login')
                    this.$refs.passnum.focus()
                }    
            },
            fnGetErrMsg(pMsg){
                // console.log(pMsg)
                if (pMsg) this.bAlert = true;
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>